.loading{
    position: relative;
    width:100%;
    height:100vh;
    background: #222;
}

.loading > p {
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    font-size:30px;
    color:#fff;
}

.loaded{
    margin: 0 auto;
    padding-bottom: 50px;
    width:100%;
    max-width: 1000px;
    color:#fff;
}

.bg{
    background: #222;
}

.title{
    padding:30px;
    text-align: center;
    font-size:4vw;
    transition: 0.1s linear;
}

@media (max-width:1200px){
    .title{font-size:50px;}
}
@media (max-width:640px){
    .title{font-size:30px;}
}

.image{
    margin: 0 auto;
    width:65%;
}

.image > img{
    width:100%;
}

.decription{
    margin-top: 30px;
    text-align: center;
    font-size: 20px;
    line-height: 1.5;
}

.genres{
    padding-top:30px;
    text-align: center;
    font-size: 32px;
}

.genreList{
    padding-top: 20px;
    display: flex;
    justify-content: center;
    gap: 15px;
}

.genreList > li:not(:last-of-type)::after{
    content: " ,";
}

.rating{
    padding:40px;
    font-size: 30px;
    text-align: center;
}
.rating > span:first-of-type{
    padding-right: 40px;
}

button{
    display: block;
    margin: 50px auto;
    padding:10px 30px;
    text-transform: uppercase;
    font-size:20px;
    border-radius: 10px;
    border:2px solid #fff;
    color:#fff;
    background: none;
    font-weight: bold;
}