.wrap{
    width:100%;
    background: #fff;
}

.title{
    position: fixed;
    width:100%;
    top: 0; left: 0;
    padding:20px 0;
    font-size: 4vw;
    text-align: center;
    color:maroon;
    background: #fff;
    border-bottom: 2px solid maroon;
    cursor: pointer;
    z-index: 50;
}
@media (max-width:1200px){
    .title{font-size:50px;}
}
@media (max-width:640px){
    .title{font-size:30px;}
}

.loader{
    padding-top: 200px;
    text-align: center;
    font-size:26px;
    font-weight: bold;
    color:maroon;
}

.center{
    margin: 0 auto;
    width:100%;
    max-width: 1200px;
}

.movie_list{
    margin-top: 150px;
    margin-bottom: 150px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
    gap:20px;
}