.box_container{
    box-shadow: 0 0 5px #222;
    position: relative;
    margin-bottom: 30px;
}

.box_container > img{
    padding:20px 0;
    display: block;
    margin: 0 auto;
}

.box_container > .box_content{
    padding:0 20px;
}

.box_container > .box_content > .title{
    width:100%;
    text-align: center;
    font-size:28px;
    color: maroon;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.box_container > .box_content > .title > .year{
    color:#333;
}

.box_container > .box_content > .genre{
    padding: 20px 0;
    display: flex;
    justify-content: center;
    gap: 10px;
    font-weight: bold;
    color:#777;
}

.box_container > .box_content > .summary{
    font-size:15px;
    line-height:1.5;
    padding-bottom: 60px;
}

.box_container > .box_content > .view{
    position: absolute;
    right: 10px; bottom: 10px;
    padding:5px 15px;
    background: #fff;
    border:1px solid maroon;
    color:maroon;
    font-size:18px;
    font-weight: bold;
    transition: 0.3s linear;
}

.box_container > .box_content > .view:hover{
    color:#fff;
    background: maroon;
}